import React from 'react';
import PropTypes from 'prop-types';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import Button from '@vkontakte/vkui/dist/components/Button/Button';
import Group from '@vkontakte/vkui/dist/components/Group/Group';
import connect from '@vkontakte/vk-connect'; 
import Cell from '@vkontakte/vkui/dist/components/Cell/Cell';
import Div from '@vkontakte/vkui/dist/components/Div/Div';
import InfoRow from '@vkontakte/vkui/dist/components/InfoRow/InfoRow';
import Progress from '@vkontakte/vkui/dist/components/Progress/Progress';
import List from '@vkontakte/vkui/dist/components/List/List';
import { FormLayout, FormLayoutGroup, Input, FormStatus, Search, CellButton, Separator } from '@vkontakte/vkui';
import { HeaderButton } from '@vkontakte/vkui';
///Icons
import Icon24Back from '@vkontakte/icons/dist/24/back';
import Icon24Search from '@vkontakte/icons/dist/24/search';
import Icon24Info from '@vkontakte/icons/dist/24/info';
import Icon24Education from '@vkontakte/icons/dist/24/education';
import Icon24UserOutgoing from '@vkontakte/icons/dist/24/user_outgoing';
import Icon24User from '@vkontakte/icons/dist/24/user';
import Icon24Newsfeed from '@vkontakte/icons/dist/24/newsfeed';
import Icon24Settings from '@vkontakte/icons/dist/24/settings';
import Icon24MoneyTransfer from '@vkontakte/icons/dist/24/money_transfer';
import Icon24BrowserBack from '@vkontakte/icons/dist/24/browser_back';
import Icon24Market from '@vkontakte/icons/dist/24/market';
import Icon24LogoVk from '@vkontakte/icons/dist/24/logo_vk';
import Icon56FavoriteOutline from '@vkontakte/icons/dist/56/favorite_outline';
import Icon24Help from '@vkontakte/icons/dist/24/help';
import Icon24Add from '@vkontakte/icons/dist/24/add';
import Icon24Services from '@vkontakte/icons/dist/24/services';
import Icon24Note from '@vkontakte/icons/dist/24/note';
import Icon24Live from '@vkontakte/icons/dist/24/live';
import Icon24Gift from '@vkontakte/icons/dist/24/gift';
import Icon24PlayNext from '@vkontakte/icons/dist/24/play_next';
import Icon24Bug from '@vkontakte/icons/dist/24/bug';
import Avatar from '@vkontakte/vkui/dist/components/Avatar/Avatar';
import user from '@vkontakte/icons/dist/24/user';

const Profile = ({ id, go, fetchedUser }) => (
	<Panel id={id}>
		 <PanelHeader left={<HeaderButton onClick={go} Data-to="Home"><Icon24BrowserBack/></HeaderButton>}>Мой профиль</PanelHeader>
		 {fetchedUser &&
		 <Group>
		<Cell before={fetchedUser.photo_200 ? <Avatar src={fetchedUser.photo_200}/> : null}	description="Пользователь">
		{`${fetchedUser.first_name} ${fetchedUser.last_name}`}</Cell>
		</Group>}
		{fetchedUser &&
		<Group>
		<Cell before={<Icon24User/>}>Информация о пользователе</Cell>
		<Separator style={{margin: '5px 0'}}/>
		<Div>
		<InfoRow title="Имя">{`${fetchedUser.first_name}`}</InfoRow><br/>
		<InfoRow title="Фамилия">{`${fetchedUser.last_name}`}</InfoRow><br/>
		<InfoRow title="Родной город">{`${fetchedUser.city.title}`}</InfoRow><br/>
		<InfoRow title="Дата рождения">{`${fetchedUser.bdate}`}</InfoRow><br/>
		</Div>
		</Group>}
		<Group title="Достижения" style={{background: '#00000'}}>
		<Div><center><img src="https://vk.com/sticker/1-9022-128"/></center></Div><br/>
		<Div><center>Увы, но у Вас нет каких-либо достижений. Чтобы их получить участвуйте в мероприятиях.<br/>
		<Separator style={{margin: '10px 0'}}/>
		<Button size="xl" level="commerce" onClick={go} Data-to="Projects">Перейти в блок "Мероприятия"</Button></center></Div>
		</Group>
		<Group>
		<Cell before={<Icon24LogoVk/>} component="a" href="https://vk.com/centobr42_press_center">Техническая поддержка</Cell>
		</Group>
 </Panel>
);

Profile.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
	fetchedUser: PropTypes.shape({
		photo_200: PropTypes.string,
		first_name: PropTypes.string,
		last_name: PropTypes.string,
		city: PropTypes.shape({
		title: PropTypes.string,
		schools: PropTypes.shape({
		name: PropTypes.string,
		year_from: PropTypes.string,	
		year_to: PropTypes.string,
		speciality: PropTypes.string,
	  }),
	}),
  }),
};

export default Profile;
